@import '../../configs/config.css';

.container {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding-left: 10px;
    background-color: var(--headerBackground);
}

.header, .footer {
    background-color: var(--headerBackground);
    padding: 10px;
    display: flex;
}

.icon {
    cursor: pointer;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left center;
    background-image: url('./images/folder-opened.png');
}

.text {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    color: var(--textColor);
    font-size: 35px;
}

.content {
    flex: 1 1 0;
    overflow: auto;
    background-color: #252525;
    display: flex;
    flex-direction: column;
}

.fileTreeContainer {
    flex: 1 1 0;
    overflow: auto;
}

.bucketContainer {
    color: white;
}

.createIconsContainer {
    flex: 1 1 0;
    display: flex;
    justify-content: flex-end;
}

.createFile {
    background-position: center;
    background-image: url('./images/file-add.png');
}

.createFolder {
    margin-left: 10px;
    background-position: center;
    background-image: url('./images/folder-add.png');
}

.minimizeFileTree{
    margin-left: 0px;
    background-position: center;
    background-image: url('./images/minimize.png');
}

.settings{
    margin-left: 0px;
    background-position: center;
    background-image: url('./images/settings.png');
}
